<template>
  <div>
    <br>
    <h3>付刚的博客</h3>
    <br>
    <div class="card" @click="link('/blogs/4')">
      <div class="card-body">
        <h4 class="card-title">浏览器播放rtmp视频</h4>
        <div class="card-subtitle mb-2 text-muted">2018年8月24日 9:58 星期二</div>
        <p class="card-text">使用浏览器的flash，实时展示rtmp直播视频流。</p>
      </div>
    </div>
    <br>
    <div class="card" @click="link('/blogs/3')">
      <div class="card-body">
        <h4 class="card-title">vue读取pdf文件并展示</h4>
        <div class="card-subtitle mb-2 text-muted">2018年8月24日 9:34 星期二</div>
        <p class="card-text">通过vue-pdf对pdf文件进行动态读取和展示，支持移动端。</p>
      </div>
    </div>
    <br>
    <div class="card" @click="link('/blogs/2')">
      <div class="card-body">
        <h4 class="card-title">vue读取markdown文件并展示</h4>
        <div class="card-subtitle mb-2 text-muted">2018年8月28日 8:42 星期二</div>
        <p class="card-text">通过markdown-loader对markdown文件进行动态读取和展示，支持代码高亮。</p>
      </div>
    </div>
    <br>
    <div class="card" @click="link('/blogs/1')">
      <div class="card-body">
        <h4 class="card-title">gulp打包混淆原生js项目</h4>
        <div class="card-subtitle mb-2 text-muted">2018年8月24日 14:00 星期五</div>
        <p class="card-text">通过gulp.js，对原生项目的html、js、css等进行压缩打包。</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  methods:{
    link:function(to){
      this.$router.push(to)
    }
  }
}
</script>



